"use client"

import { useState } from "react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import { ScrollArea } from "@/components/ui/scroll-area"
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import {
  Shield,
  Search,
  Ban,
  UserX,
  MessageSquare,
  AlertTriangle,
  RotateCcw,
  Clock,
  Filter,
  Download,
  ArrowUpDown
} from "lucide-react"
import { useMinecraft } from "@/contexts/MinecraftContext"

export default function PunishmentsPage() {
  const { players, banPlayer, mutePlayer, warnPlayer } = useMinecraft()
  
  // Extrair punições dos jogadores
  interface Punishment {
    id: string
    type: "ban" | "kick" | "mute" | "warn"
    player: string
    reason: string
    staffMember: string
    date: Date
    duration?: number
    isActive: boolean
  }

  const punishments: Punishment[] = players.flatMap(player => 
    player.punishments.map(p => ({
      ...p,
      player: player.username
    }))
  )
  const [search, setSearch] = useState("")
  const [filterType, setFilterType] = useState<string>("all")
  const [filterStatus, setFilterStatus] = useState<string>("all")
  const [sortBy, setSortBy] = useState<keyof Punishment>("date")
  const [sortOrder, setSortOrder] = useState<"asc" | "desc">("desc")
  const [selectedPlayer, setSelectedPlayer] = useState<string | null>(null)
  const [punishmentType, setPunishmentType] = useState<string>("warn")
  const [reason, setReason] = useState("")
  const [duration, setDuration] = useState("")

  const filteredPunishments = punishments
    .filter((p: Punishment) => {
      const matchesSearch = search === "" ||
        p.player.toLowerCase().includes(search.toLowerCase()) ||
        p.reason.toLowerCase().includes(search.toLowerCase())
      const matchesType = filterType === "all" || p.type === filterType
      const matchesStatus = filterStatus === "all" || 
        (filterStatus === "active" ? p.isActive : !p.isActive)
      return matchesSearch && matchesType && matchesStatus
    })
    .sort((a: Punishment, b: Punishment) => {
      const order = sortOrder === "asc" ? 1 : -1

      if (sortBy === "date") {
        return (a.date.getTime() - b.date.getTime()) * order
      }

      if (sortBy === "player" || sortBy === "reason" || sortBy === "type" || sortBy === "staffMember") {
        return a[sortBy].localeCompare(b[sortBy]) * order
      }

      if (sortBy === "duration" && a.duration && b.duration) {
        return (a.duration - b.duration) * order
      }

      return 0
    })

  const handleSort = (column: keyof Punishment) => {
    if (sortBy === column) {
      setSortOrder(sortOrder === "asc" ? "desc" : "asc")
    } else {
      setSortBy(column)
      setSortOrder("asc")
    }
  }

  const handleAddPunishment = async () => {
    if (!selectedPlayer || !reason) return

    const durationMinutes = duration ? parseInt(duration) : undefined

    switch (punishmentType) {
      case "ban":
        await banPlayer(selectedPlayer, reason, durationMinutes)
        break
      case "mute":
        await mutePlayer(selectedPlayer, reason, durationMinutes)
        break
      case "warn":
        await warnPlayer(selectedPlayer, reason)
        break
      default:
        break
    }

    setSelectedPlayer(null)
    setReason("")
    setDuration("")
  }

  const formatDuration = (minutes?: number) => {
    if (!minutes) return "Permanente"
    if (minutes < 60) return `${minutes}m`
    if (minutes < 1440) return `${Math.floor(minutes / 60)}h`
    return `${Math.floor(minutes / 1440)}d`
  }

  const formatDate = (date: Date) => {
    return new Date(date).toLocaleString('pt-BR', {
      day: '2-digit',
      month: '2-digit',
      year: '2-digit',
      hour: '2-digit',
      minute: '2-digit'
    })
  }

  const getPunishmentColor = (type: string) => {
    switch (type) {
      case "ban": return "text-red-500"
      case "mute": return "text-yellow-500"
      case "kick": return "text-orange-500"
      default: return "text-blue-500"
    }
  }

  const getPunishmentIcon = (type: string) => {
    switch (type) {
      case "ban": return <Ban className="h-4 w-4" />
      case "mute": return <MessageSquare className="h-4 w-4" />
      case "kick": return <UserX className="h-4 w-4" />
      default: return <AlertTriangle className="h-4 w-4" />
    }
  }

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold text-white">Punições</h1>
          <p className="text-gray-400">Gerencie as punições do servidor</p>
        </div>
        <div className="flex items-center space-x-2">
          <Dialog>
            <DialogTrigger asChild>
              <Button className="bg-red-600 hover:bg-red-700">
                <Shield className="mr-2 h-4 w-4" />
                Nova Punição
              </Button>
            </DialogTrigger>
            <DialogContent className="bg-gray-800 border-gray-700">
              <DialogHeader>
                <DialogTitle className="text-white">Aplicar Nova Punição</DialogTitle>
                <DialogDescription className="text-gray-400">
                  Preencha os detalhes da punição abaixo
                </DialogDescription>
              </DialogHeader>

              <div className="space-y-4">
                <div>
                  <label className="text-sm text-gray-400">Jogador</label>
                  <Select value={selectedPlayer || ""} onValueChange={setSelectedPlayer}>
                    <SelectTrigger className="bg-gray-700 border-gray-600">
                      <SelectValue placeholder="Selecione um jogador" />
                    </SelectTrigger>
                    <SelectContent className="bg-gray-800 border-gray-700">
                      {players.map((player) => (
                        <SelectItem key={player.id} value={player.id}>
                          {player.username}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>

                <div>
                  <label className="text-sm text-gray-400">Tipo</label>
                  <Select value={punishmentType} onValueChange={setPunishmentType}>
                    <SelectTrigger className="bg-gray-700 border-gray-600">
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent className="bg-gray-800 border-gray-700">
                      <SelectItem value="warn">Advertência</SelectItem>
                      <SelectItem value="mute">Mute</SelectItem>
                      <SelectItem value="kick">Kick</SelectItem>
                      <SelectItem value="ban">Ban</SelectItem>
                    </SelectContent>
                  </Select>
                </div>

                <div>
                  <label className="text-sm text-gray-400">Motivo</label>
                  <Input
                    value={reason}
                    onChange={(e) => setReason(e.target.value)}
                    className="bg-gray-700 border-gray-600"
                  />
                </div>

                {(punishmentType === "ban" || punishmentType === "mute") && (
                  <div>
                    <label className="text-sm text-gray-400">Duração (minutos)</label>
                    <Input
                      type="number"
                      placeholder="Deixe vazio para permanente"
                      value={duration}
                      onChange={(e) => setDuration(e.target.value)}
                      className="bg-gray-700 border-gray-600"
                    />
                  </div>
                )}
              </div>

              <DialogFooter>
                <Button variant="ghost">Cancelar</Button>
                <Button 
                  onClick={handleAddPunishment}
                  disabled={!selectedPlayer || !reason}
                  className="bg-red-600 hover:bg-red-700"
                >
                  Aplicar Punição
                </Button>
              </DialogFooter>
            </DialogContent>
          </Dialog>

          <Button variant="outline" className="border-green-600 text-green-500">
            <Download className="mr-2 h-4 w-4" />
            Exportar
          </Button>
        </div>
      </div>

      <Card className="bg-gray-800 border-gray-700">
        <CardHeader>
          <div className="flex items-center justify-between">
            <CardTitle className="text-white flex items-center">
              <Shield className="mr-2 h-5 w-5" />
              Lista de Punições
            </CardTitle>
            <div className="flex items-center space-x-2">
              <div className="relative w-64">
                <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
                <Input
                  placeholder="Buscar punições..."
                  value={search}
                  onChange={(e) => setSearch(e.target.value)}
                  className="pl-10 bg-gray-700 border-gray-600"
                />
              </div>

              <Select value={filterType} onValueChange={setFilterType}>
                <SelectTrigger className="w-40 bg-gray-700 border-gray-600">
                  <SelectValue placeholder="Tipo" />
                </SelectTrigger>
                <SelectContent className="bg-gray-800 border-gray-700">
                  <SelectItem value="all">Todos os tipos</SelectItem>
                  <SelectItem value="warn">Advertências</SelectItem>
                  <SelectItem value="mute">Mutes</SelectItem>
                  <SelectItem value="kick">Kicks</SelectItem>
                  <SelectItem value="ban">Bans</SelectItem>
                </SelectContent>
              </Select>

              <Select value={filterStatus} onValueChange={setFilterStatus}>
                <SelectTrigger className="w-40 bg-gray-700 border-gray-600">
                  <SelectValue placeholder="Status" />
                </SelectTrigger>
                <SelectContent className="bg-gray-800 border-gray-700">
                  <SelectItem value="all">Todos</SelectItem>
                  <SelectItem value="active">Ativos</SelectItem>
                  <SelectItem value="expired">Expirados</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </div>
        </CardHeader>
        <CardContent>
          <ScrollArea className="h-[600px]">
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Tipo</TableHead>
                  <TableHead>
                    <Button
                      variant="ghost"
                      onClick={() => handleSort("player")}
                      className="hover:text-white"
                    >
                      Jogador
                      <ArrowUpDown className="ml-2 h-4 w-4" />
                    </Button>
                  </TableHead>
                  <TableHead>
                    <Button
                      variant="ghost"
                      onClick={() => handleSort("reason")}
                      className="hover:text-white"
                    >
                      Motivo
                      <ArrowUpDown className="ml-2 h-4 w-4" />
                    </Button>
                  </TableHead>
                  <TableHead>
                    <Button
                      variant="ghost"
                      onClick={() => handleSort("date")}
                      className="hover:text-white"
                    >
                      Data
                      <ArrowUpDown className="ml-2 h-4 w-4" />
                    </Button>
                  </TableHead>
                  <TableHead>Duração</TableHead>
                  <TableHead>Staff</TableHead>
                  <TableHead>Status</TableHead>
                  <TableHead>Ações</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {filteredPunishments.map((punishment) => (
                  <TableRow key={punishment.id}>
                    <TableCell>
                      <div className={`flex items-center ${getPunishmentColor(punishment.type)}`}>
                        {getPunishmentIcon(punishment.type)}
                        <span className="ml-2 capitalize">{punishment.type}</span>
                      </div>
                    </TableCell>
                    <TableCell>
                      <div className="flex items-center space-x-2">
                        <Avatar className="h-6 w-6">
                          <AvatarImage src={`https://mc-heads.net/avatar/${punishment.player}`} />
                          <AvatarFallback>{punishment.player[0]}</AvatarFallback>
                        </Avatar>
                        <span>{punishment.player}</span>
                      </div>
                    </TableCell>
                    <TableCell>{punishment.reason}</TableCell>
                    <TableCell>{formatDate(punishment.date)}</TableCell>
                    <TableCell>{formatDuration(punishment.duration)}</TableCell>
                    <TableCell>{punishment.staffMember}</TableCell>
                    <TableCell>
                      <Badge
                        variant={punishment.isActive ? "default" : "secondary"}
                        className={punishment.isActive ? "bg-green-600" : "bg-gray-600"}
                      >
                        {punishment.isActive ? "Ativo" : "Expirado"}
                      </Badge>
                    </TableCell>
                    <TableCell>
                      {punishment.isActive && (
                        <Button
                          variant="ghost"
                          size="sm"
                      onClick={() => {
                        // Aqui você pode implementar a lógica para revogar a punição
                        // Por exemplo, adicionando um método no contexto para isso
                        console.log("Revogar punição:", punishment.id)
                      }}
                          className="h-8 w-8 p-0"
                        >
                          <RotateCcw className="h-4 w-4" />
                        </Button>
                      )}
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </ScrollArea>
        </CardContent>
      </Card>
    </div>
  )
}
